body{
    margin:0;
    padding:0;
    font-family: Arial, Helvetica, sans-serif;
    background: #ffffff;
}
.mainContainer{
    width: 800px;
    margin: 0 auto;
    padding-top:80px;
}
.block-wrapper{
    display: flex;
    justify-content: center;
}
.block-wrapper button{
    background:none;
    text-decoration: none;
    color:rgba(0,0,0,.6);
    font-size: 18px;
    width:150px;
    height:150px;
    border:solid 8px rgba(0,0,255,.3);
    margin:20px;
    border-radius:8px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.block-wrapper button:focus{
    outline: none;
    border:solid 8px rgba(0,0,255,.9);
    color:rgba(0,0,0,1);
}
#keyOutput{
    padding:40px;
    text-align: center;
}
.formRow{
    width:400px;
    margin:0 auto;
}
.buttonRow{
    text-align:center;
}
label{
    display: block;
    font-size:18px
}
textarea{
    width:100%;
    border-radius: 8px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;
    padding:10px;
}
.btn{
    width:150px;
    background:rgba(0,0,255,.6);
    padding:10px;
    font-size: 18px;
    color:#ffffff;
    border:none;
    margin-top: 10px;
    border-radius: 5px;
}
.btn:focus{
    outline: none;
    background: rgba(0,0,255,.9);
}
h1{
    margin:0;
    padding:0 0 10px 0;
    font-size:30px;
    text-align: center;
    color:rgba(0,0,0,.9);
}
.label{
    padding-bottom: 5px;
}
.success{
    position: absolute;
    top:20px;
    left:50%;
    transform: translateX(-50%);
    -webkit-transform:translateX(-50%);
    background:#b9f5aa;
    border:solid 1px #4CAF50;
    border-radius: 5px;
    padding:10px;
    width:400px;
    margin:0 auto;
}
.close{
    background:none;
    border:none;
    font-size:20px;
    color:#fff;
    float: right;
    margin-top:-8px;
}
.close:focus{
outline:none;
color:green
}
.backkey{
    color:green
}